  .card-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  .centered-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 90vh;
  }
  
  .centered-card {
    width: 120px;
    height: 120px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #ccc;
    border-radius: 40px;
    margin: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    background-color: #f9f9f9;
  }
  

  
  .module-card img {
    border-radius: 15px 15px 0 0;
  }
  
  .module-card .btn {
    border-radius: 50%;
    padding: 0.5rem;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .navbar-brand {
    font-weight: bold;
  }
  