    monospace;
}

.App {
  display: flex;
  width: 100%;
  align-items: stretch;
}
table.table-fit tfoot td {
  width: auto !important;
}

body {
  /*font-family: 'Poppins', sans-serif;*/
  font-family: "Nunito Sans", sans-serif;
  /*background: #fafafa;*/
  background-color: #f5f9fc;
}

a,
a:hover,
a:focus {
  color: inherit;
  text-decoration: none;
  transition: all 0.2s;
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */
.sidebar-rtl {
  min-width: 250px;
  max-width: 250px;
  background: #354052;
  color: #adb5bd;
  margin-right: -250px;
  transition: all 0.5s;
  min-height: 100vh !important;
}

.sidebar-rtl.is-open {
  margin-right: 0px;
  transition: 0.5s;
  min-height: 100vh !important;
}

/* .jumbotron {
  padding: 2rem 1rem;
  margin-bottom: 2rem;
  background-color: #94bf84;
  border-radius: .3rem;
} */

.flexbox-container {
  display: flex;
  align-items: center;
  height: calc(var(--vh, 1vh) * 100);
}

.sidebar {
  min-width: 250px;
  max-width: 250px;
  background: #354052;
  color: #adb5bd;
  margin-left: -250px;
  transition: all 0.5s;
  min-height: 100vh !important;
}

.sidebar.is-open {
  margin-left: 0px;
  transition: 0.5s;
  min-height: 100vh !important;
}

.sidebar-header {
  background: #313b4c;
  color: #adb5bd;
}

.sidebar-header h3 {
  color: #fff;
  padding: 1em;
}

.sidebar ul p {
  color: #fff;
  padding: 10px;
}

.collapse.show,
.collapsing {
  background: #313b4c;
}

.nav-item {
  /*color: #ced4da;*/
  color: #adb5bd;
  font-weight: 300;
}

.nav-item.open {
  color: #e9ecef;
  background: #313b4c;
}


.nav-item .nav-link .active {
  /* background: #2d3646 !important; */
  background: #abbee4;
  color: #fff;
}

.nav-item .active {
  /* background: #2d3646 !important; */
  background: #abbee4;
  color: #fff;
}

.nav-item:hover {
  /* background: #313b4c; */
  background: #abbee4;
  color: #fff;
  transition: 0.2s;
}

.nav-item .svg-inline--fa {
  color: inherit;
}

li a.dropdown-toggle::after {
  display: inline-flex;
  position: relative;
  left: 60%;
  top: 10%;
}

.sidebar-header > button {
  position: relative;
  float: right;
  margin: 0.5em;
  font-size: 2rem;
  cursor: pointer;
  display: none;
}

/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */
.content {
  padding: 20px;
  margin-left: 0;
  height: auto;
}
.alert {
  position: relative;
  padding: 0.2rem 1rem;
  margin-bottom: .2rem;
  border: 1px solid transparent;
  border-radius: 0.25rem;
}
.card-form-title-dark{
  width: 100%; 
  background-color: #2f324d; 
  color: white; 
  padding: 5px;
  border-radius: 3px;
}
.card-form-title-light{
  width: 100%; 
  background-color: none; 
  color: black; 
  padding: 5px;
  border-radius: 3px;
}

@media only screen and (max-width: 575.98px) {
  body {
    overflow: hidden;
  }

  .content.is-open {
    /*margin-left: 100%;*/
    display: none;
    transition: all 0.5s;
  }

  .sidebar.is-open {
    min-width: 100%;
    max-width: 100%;
    margin-left: 0;
    transition: all 0.5s, height 0s;
    height: 100vh !important;
  }

  .sidebar.is-open > .sidebar-header button {
    display: block;
  }

  li a.dropdown-toggle::after {
    display: inline-block;
    position: relative;
    left: 68%;
  }
}

