/* Light Theme */
.basic-form.light {
  background-color: #ffffff;
  color: #000000;
}

/* Dark Theme */
.basic-form.dark {
  background-color: #222222;
  color: #ffffff;
}

.basic-form {
    width: 1000px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #f7f7f7;
  }
  
  .form-title {
    font-size: 24px;
    margin-bottom: 20px;
    text-align: left; /* Updated to left alignment */
  }
  
  .submit-btn {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: navy;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .form-group {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .label {
    width: 30%;
    text-align: right;
    padding-right: 12px;
    padding-left: 5px;
    /* color: black; */
    font-weight: bold;
  }
  
  .input {
    width: 70%;
    padding-right: 12px;
    padding-left: 12px;
  }
  
